import React from 'react'
import { ResultPostRoute } from './NavbarSearch'
import styled from 'styled-components'
import Link from 'gatsby-link'
import { ellipsis } from 'polished'
import media from '../styles/media'

const Wrap = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  top: 2.1rem;
  right: -4.5rem;
  width: 300px;
  padding: 0;
  border-radius: 6px;
  border: 1px solid #2e2e2e;
  box-shadow: 1px 1px 5px rgba(46, 46, 46, 0.3);
  background-color: #fff;

  ${media.iPhone5} {
    width: 250px;
  }

  ${media.Phablet} {
    width: 450px;
    right: 0;
  }
`

const NoResult = styled.div`
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 3rem 1rem;
  text-align: center;
  font-weight: bold;
`

const RouteLink = styled(Link)`
  ${ellipsis('100%')};
  display: block;
  width: 100%;
  padding: 0.75rem 1.5rem;
  font-size: 0.85rem;
  margin: 0;
  margin: 0;

  &:not(:last-child) {
    border-bottom: 1px solid #2e2e2e;
  }

  &:hover {
    background-color: rgba(42, 42, 42, 0.1);
  }
`

const ResultSectionHeader = styled.div`
  padding: 0.45rem 1rem;
  font-size: 0.8rem;
  color: #fff;
  background-color: #24272e;
`

const MAX_REULST_VISIBLE = 7

export default ({
  resultPostRoutes = [],
  resultTagRoutes = [],
  isVisible = false,
}: {
  resultRoutes: ResultPostRoute[],
}) => {
  const resultPostRoutesToShow = resultPostRoutes.slice(0, MAX_REULST_VISIBLE)
  const resultTagRoutesToShow = resultTagRoutes.slice(0, MAX_REULST_VISIBLE)

  return (
    isVisible && (
      <Wrap>
        {resultPostRoutes.length === 0 && resultTagRoutes.length === 0 ? (
          <NoResult>검색 결과가 없습니다.</NoResult>
        ) : (
          <div>
            {resultPostRoutesToShow.length > 0 && (
              <div>
                <ResultSectionHeader>포스트</ResultSectionHeader>
                {resultPostRoutesToShow.map((route, i) => (
                  <RouteLink key={i} to={route.data.route}>
                    {route.data.title}
                  </RouteLink>
                ))}
              </div>
            )}
            {resultTagRoutesToShow.length > 0 && (
              <div>
                <ResultSectionHeader>태그</ResultSectionHeader>
                {resultTagRoutesToShow.map(tagRoute => (
                  <RouteLink key={tagRoute.name} to={tagRoute.route}>
                    {tagRoute.name}
                  </RouteLink>
                ))}
              </div>
            )}
          </div>
        )}
      </Wrap>
    )
  )
}



