import React from 'react'
import Link from 'gatsby-link'
import styled from 'styled-components'
import { clearFix } from 'polished'
import styles from '../styles'
import { ContentWrapper } from '../components/content-wrapper'
import NavbarSearch from '../components/NavbarSearch'

const Wrap = styled.nav`
  ${clearFix()};
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(to right, #4568dc, #b06ab3);
  border-bottom: 1px solid ${styles.colors.border};
`

const NavbarCotent = ContentWrapper.extend`
  display: flex;
  align-items: center;
`

const SiteTitle = styled.div`
  margin-right: auto;
  color: #fff;
  font-weight: 400;
  font-size: 1.2rem;
  font-family: 'Roboto', sans-serif;
  line-height: ${styles.sizes.topNavHeight};
  letter-spacing: -1px;
  font-weight: 400;
`

const NavbarSearchWrap = styled.div`
  margin-left: auto;
`

const MenuWrap = styled.ul`
  margin: 0 0 0 1rem;
  list-style: none;
  padding-left: 0;
`

const Menu = styled(Link)`
  float: left;
  line-height: ${styles.sizes.topNavHeight};
  margin-bottom: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  color: #fff;

  &:not(:first-child) {
    margin-left: 0.5rem;
  }
`

class NavBar extends React.Component {
  render() {
    return (
      <Wrap>
        <NavbarCotent>
          <Link to={'/'}>
            <SiteTitle>rhostem.github.io</SiteTitle>
          </Link>
          <NavbarSearchWrap>
            <NavbarSearch />
          </NavbarSearchWrap>
          <MenuWrap>
            {/* <Menu to={'/tags'}>Tag</Menu> */}
            <Menu to={'/archive'}>Archive</Menu>
          </MenuWrap>
        </NavbarCotent>
      </Wrap>
    )
  }
}

NavBar.propTypes = {}

export default NavBar



