import { actions } from '../../static/js/searchUtil'
let searchWorker = null

// worker 스레드 사이에서 주고받을 데이터 형태
export type Message = {
  action: string,
  payload: any,
}

// search-worker web worker 스레드를 생성하고, 중개한다.
export default {
  // 초기화
  init: () => {
    if (window.Worker && !searchWorker) {
      searchWorker = new Worker('/js/search-worker.js')
    }
  },

  /**
   * onmessage 콜백 등록
   */
  addMessageListener: ({ onReceiveSearchResult = () => {} }) => {
    searchWorker.onmessage = e => {
      const message: Message = e.data

      switch (message.action) {
        case actions.SEARCH_SUCCESS:
          const { resultPostRoutes, resultTagRoutes, searchTime } = message.payload

          onReceiveSearchResult({
            resultPostRoutes,
            resultTagRoutes,
            searchTime,
          })
          break

        default:
          break
      }
    }
  },

  // 검색
  find: (searchInput = '') => {
    searchWorker.postMessage({
      action: actions.SEARCH_REQUEST,
      payload: {
        searchInput,
      },
    })
  },
}



