import * as React from 'react'
import styled from 'styled-components'
import styles from '../styles'
import { ContentWrapper } from '../components/content-wrapper'
import SITE_CONFIG from '../../site-config'

const FooterWrap = styled.footer`
  display: flex;
  flex-direction: row;
  align-items: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: ${styles.sizes.footerHeight};
  padding: 0.5rem 0;
  background-color: #fff;
  border-top: 1px solid ${styles.colors.border};
  font-family: 'Roboto', sans-serif;
  line-height: 1.4;
  text-align: center;
`

const FooterContent = ContentWrapper.extend`
  font-family: 'Roboto';
  font-weight: 400;
  font-size: 0.7rem;

  ${styles.media.Phablet} {
    font-size: 0.75rem;
  }
`

const CopyRight = styled.div`
  /* float: left; */
`

const OwnerLink = styled.a`
  color: ${styles.colors.text};
  text-decoration: underline;
`

const FooterLinks = styled.div`
  text-align: center;
  font-size: 0.75rem;
  margin-top: 0.5rem;

  ${styles.media.Phablet} {
    font-size: 0.85rem;
  }

  & > * {
    margin-left: 0.5em;
  }

  i {
    color: ${styles.colors.text};
  }
`

function Footer() {
  return (
    <FooterWrap>
      <FooterContent>
        <CopyRight>
          <span>Copyright © 2018. </span>
          <OwnerLink href="https://www.github.com/rhostem">rhostem</OwnerLink>
          <span> All rights reserved</span>
        </CopyRight>
        <FooterLinks>
          <a href={SITE_CONFIG.githubUrl}>
            <i className="fa fa-github" />
          </a>
          <a href={`mailto:${SITE_CONFIG.authorEmail}`}>
            <i className="fa fa-envelope-o" />
          </a>
          <a href={`${SITE_CONFIG.siteRss}`}>
            <i className="fa fa-rss" />
          </a>
        </FooterLinks>
      </FooterContent>
    </FooterWrap>
  )
}

export default Footer



