import React, { Component } from 'react'
import { debounce } from 'throttle-debounce'
import styled from 'styled-components'
import { centeredY } from '../styles/mixins'
import SearchResult from './SearchResult'
import media from '../styles/media'
import SearchService from '../service/SearchService'

const Wrap = styled.div`
  position: relative;
  height: 1.8rem;
`

const SearchInput = styled.input`
  height: 100%;
  border: none;
  outline: none;
  padding: 0 1rem 0 1.8rem;
  background-color: transparent;
  border-radius: 6px;
  color: white;
  font-size: 0.9rem;
  width: 30px;

  &:focus {
    ${media.iPhone5} {
      width: 100px;
    }
    width: 130px;
    background-color: rgba(0, 0, 0, 0.1);
  }

  ${media.Phablet} {
    width: 180px;
    &:focus {
      width: 180px;
    }
  }
`

const SearchIcon = styled('i')`
  ${centeredY};
  left: 0.4em;
  color: #fff;
`

export type RouteData = {
  title: string,
  route: title,
  tags: string[],
}

export type RouteMap = {
  [routeId: number]: RouteData,
}

export type WordMap = {
  [wordId: string]: {
    r: number[],
  },
}

export type ResultPostRoute = {
  data: RouteData,
  point: number, // 검색 매칭 포인트
}
export type ResultTagRoute = {
  name: string,
  route: string,
}

type Props = {}
type State = {
  resultPostRoutes: ResultPostRoute[],
  resultTagRoutes: ResultTagRoute[],
}

class NavbarSearch extends Component<Props, State> {
  wordList: string[] = []
  routeMap: RouteMap = {}
  wordMap: WordMap = {}

  constructor(props) {
    super(props)
    this.state = {
      search: '',
      isFocused: false,
      isResultReady: false,
      resultPostRoutes: [], // 결과 포스트 라우트 목록
      resultTagRoutes: [], // 결과 태그 라우트 목록
    }
  }

  componentDidMount() {
    SearchService.init()
    SearchService.addMessageListener({
      onReceiveSearchResult: this.handleReceiveSearchResult,
    })
  }

  /**
   * web worker에서 발생하는 onmessage 콜백을 ㅇ등록한다.
   */
  handleReceiveSearchResult = ({ resultPostRoutes, resultTagRoutes, searchTime }) => {
    console.log(`searchTime`, searchTime)

    this.setState({
      resultPostRoutes,
      resultTagRoutes,
      isResultReady: true,
    })
  }

  handleChangeInput = e => {
    this.setState({ search: e.target.value })
    this.search(e.target.value)
  }

  search = debounce(400, input => {
    this.setState({
      isResultReady: false,
    })
    SearchService.find(input)
  })

  render() {
    return (
      <Wrap
        onFocus={() =>
          this.setState({
            isFocused: true,
          })
        }
        onBlur={() => {
          setTimeout(() => {
            this.setState({
              isFocused: false,
              isResultReady: false,
            })
          }, 200)
        }}
      >
        <SearchIcon className="fa fa-search" />
        <SearchInput
          value={this.state.search}
          onChange={this.handleChangeInput}
          type="text"
          placeholder="포스트 검색"
        />
        <SearchResult
          resultPostRoutes={this.state.resultPostRoutes}
          resultTagRoutes={this.state.resultTagRoutes}
          isVisible={this.state.isResultReady}
        />
      </Wrap>
    )
  }
}

export default NavbarSearch



